%l-main,
.l-main {
  position: relative;
  margin: 0 auto;
  background-color: palette(grey, xlight);
  max-width: $bp-max;
  overflow: hidden;
}

.l-main--white {
  @extend %l-main;

  background-color: $white;
}

.l-main__content {
  background-color: palette(grey, light);
  padding: ru(1);
  min-height: ru(10);

  // @include respond-to('full-nav') {
